.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

html,
body {
  padding: 0;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Nunito", Arial,
    Helvetica, sans-serif, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell",
    "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  --red: #ff5000 !important;
  --salmon: #ffdbd0;
  --pink: #ffdbd0;
  --beige: #f7f0f0;
  --white: rgb(247, 239, 240);
  --material-border: 0.5px solid rgb(233, 233, 233);
}

a {
  color: inherit;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

* {
  box-sizing: border-box;
}

p {
  margin: 0px;
}

*:focus {
  outline: none !important;
}

.MuiSwitch-colorPrimary.Mui-checked {
  color: rgb(255, 97, 8) !important;
}

.MuiSwitch-track {
  background: rgb(122, 122, 122) !important;
}

.MuiSwitch-colorPrimary.Mui-checked + .MuiSwitch-track {
  background: rgb(255, 112, 8) !important;
}

.MuiButtonBase-root.MuiChip-root.chip-select-item.MuiChip-colorPrimary.MuiChip-clickableColorPrimary.MuiChip-outlined.MuiChip-outlinedPrimary.MuiChip-clickable {
  border: 1px solid rgba(0, 0, 0, 0.23) !important;
  color: rgba(0, 0, 0, 0.87) !important;
}

.loader-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 30vh;
}

.Mui-focused .MuiOutlinedInput-notchedOutline.custom {
  border-color: var(--red) !important;
  border-width: 1px !important;
}


.MuiButton-outlinedPrimary {
  color: var(--red) !important;
  border: solid 1px rgba(242, 75, 13, 0.61) !important;
}

.bold {
  font-weight: bold !important;
}

.dark-blue-color {
  color: #303030 !important;
}

@media only screen and (max-width: 600px) {
  .container.increase-padding {
    padding: 0px 25px;
  }
}

@media screen and (max-width: 360px) {
  .MuiPaper-root.MuiDialog-paper {
    margin-left: 10px;
    margin-right: 10px;
  }
}

.tenant-footer-container {
  background-color: var(--pink);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.tenant-footer-content {
  width: 100%;
  max-width: 1024px;
}

.tenant-footer-header {
  font-size: 22px;
  font-weight: bold;
}

.tenant-footer-container .MuiSvgIcon-root {
  font-size: 28px;
}

.tenant-footer-container ul {
  padding: 0px !important;
}

.tenant-footer-container ul {
  margin-top: 20px;
}

.tenant-footer-container li {
  list-style-type: none;
  font-size: 16px;
  margin-top: 10px;
}

.tenant-footer-container a:hover {
  text-decoration: none;
  font-weight: 600;
}

.tenant-footer-container li a {
  color: var(--red);
}

.tenant-footer-container li a:hover {
  text-decoration: none;
  font-weight: 600;
}

#houm-footer-logo {
  height: 75px;
}

.logo-social-buttons-container {
  position: relative;
}

@media only screen and (max-width: 769px) {
  #logo-buttons-flex-generator {
    width: 100%;
    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }

  #orange-footer-button {
    margin-top: 40px;
    background: var(--red);
    width: 50px;
    height: 50px;
    color: white;
    margin-right: 15px;
  }

  .tenant-footer-container .col-md-4 {
    margin-bottom: 40px;
  }

  .tenant-footer-container {
    padding: 60px 20px;
  }
}

@media only screen and (min-width: 770px) {
  #logo-buttons-flex-generator {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  .tenant-footer-container {
    padding: 60px 0px;
  }

  #orange-footer-button {
    background: var(--red);
    width: 60px;
    height: 60px;
    color: white;
    margin-right: 20px;
  }
}

.footer-payment__place-to-pay {
  margin-top: 20px;
  width: 150px;
}

.chip-select-item {
  display: inline !important;
  padding-top: 8px !important;
  text-align: center !important;
  font-size: 18px !important;
}

.chip-select {
  display: flex;
  overflow-x: scroll;
  -ms-overflow-style: none;
}

.chip-select::-webkit-scrollbar {
  display: none;
}

.mapboxgl-map{
    width: 100% !important;
    height: 100% !important;
}

.marker {
  background-image: url(data:image/png;base64,);
  background-size: cover;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  cursor: pointer;
}

.marker-blue {
  background-image: url(data:image/png;base64,);
  background-size: cover;
  width: 45px;
  height: 45px;
  border-radius: 50%;
  cursor: pointer;
}

.mapboxgl-popup {
  padding: 5px;
}

.mapboxgl-popup-content {
  text-align: center;
  font-family: 'Open Sans', sans-serif;
  font-size: 15px;
  padding: 15px;
}

